/* Slider */

.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-ms-touch-action: pan-y;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
}
.slick-list {
	position: relative;
	overflow: hidden;
	display: block;
	margin: 0;
	padding: 0;

	&:focus {
			outline: none;
	}

	&.dragging {
			cursor: pointer;
			cursor: hand;
	}
}
.slick-slider .slick-track,
.slick-slider .slick-list {
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	-o-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.slick-track {
	position: relative;
	left: 0;
	top: 0;
	display: block;
	margin-left: auto;
	margin-right: auto;

	&:before,
	&:after {
			content: "";
			display: table;
	}

	&:after {
			clear: both;
	}

	.slick-loading & {
			visibility: hidden;
	}
}
.slick-slide {
	float: left;
	height: 100%;
	min-height: 1px;
	[dir="rtl"] & {
			float: right;
	}
	img {
			display: block;
			max-width: 100%;
	}
	&.slick-loading img {
			display: none;
	}

	display: none;

	&.dragging img {
			pointer-events: none;
	}

	.slick-initialized & {
			display: block;
	}

	.slick-loading & {
			visibility: hidden;
	}

	.slick-vertical & {
			display: block;
			height: auto;
			border: 1px solid transparent;
	}
}
.slick-arrow.slick-hidden {
	display: none;
}
.slick-dots{
	margin: 0;
	padding: 0;
	list-style: none;
	text-align: center;

	li{
		display: inline-block;
		margin: 0 5px;

		&.slick-active button{
			background-color: rgba(#000000, .2);
		}

		button{
			overflow: hidden;
			width: 10px; height: 10px;
			padding: 0; margin: 0;
			text-indent: -3000px;
			border-radius: 50%;
			border: none;
			background: #000000;
		}
	}
}